.container {
  display: flex;
  height: 100vh;
  background: url("../../../../src/assets/gif/Authentication.gif");

  background-size: contain;
  background-repeat: no-repeat;
  background-position: left;
  background-repeat: no-repeat;
  flex-direction: row;
  justify-content: flex-end;

  padding: 5%;
  margin: 0;
  overflow: hidden;
}
.qrCode {
  height: 100px;
  width: 40%;
  position: absolute;
  right: 8%;
  top: 0;
}

.btnContainer {
  position: relative;
  top: 70%;
  height: 3rem;
  right: 24%;
  outline: none;
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  border-radius: 6px;
  outline: none;
  align-items: center;
  background: #c21f4a;
  color: #fff;
  font-weight: bold;
}

.result {
  position: relative;
  top: 50%;
  height: 4rem;
  width: 40%;
  text-align: center;
  min-width: 400px;
  font-size: 18px;
  /* color: rgba(0, 168, 89, 1); */
}
.logoContainer {
  position: absolute;
  z-index: 999;
  top: 5px;
  left: 10px;
  width: 70px;
  height: 70px;
}

.logoContainer img {
  height: 100%;
  width: 100%;
}

.imgContainer {
  position: relative;
  left: 20%;
  height: 100px;
  width: 100px;
}

.imgContainer img {
  height: 100%;
  width: 100%;
}

.home {
  display: flex;
  height: 100vh;
  background: linear-gradient(280deg, rgba(0, 0, 0, 0), rgb(5, 5, 5)),
    url("../../../assets/clock.jpg");

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-repeat: no-repeat;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 5%;
  margin: 0;
  overflow: hidden;
}

.home h1 {
  font-size: 4rem;
  padding: 0;
  margin-top: 5px;
  margin-bottom: -10px;
  color: white;
}
.logo {
  width: 50%;
  display: flex;
  height: 14rem;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  margin-bottom: 1rem;
}
.logo img {
  object-fit: contain;
  margin-left: -120px;
  max-height: 30rem;
}

/* Media Query */

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .container {
    flex-direction: column;
    align-items: center;
    padding-bottom: 10%;
    background: url("../../../../src/assets/gif/Authentication.gif");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-repeat: no-repeat;
  }

  .result {
    display: flex;
    justify-items: center;

    width: 80%;
    text-align: center;
    position: static;
    font-size: 0.8rem;
    top: 60;
    min-width: 100px;
  }

  .btnContainer {
    height: 2.5rem;
    font-size: 0.6rem;
    position: static;
    outline: none;
    padding: 0.8rem 1.6rem;
    display: flex;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    border: none;
    border-radius: 6px;
    outline: none;
    align-items: center;
    background: rgba(0, 168, 89, 1);
    color: #fff;
  }

  .qrCode {
    height: 300px;
    width: 40%;
    right: 30%;
    top: 20%;
  }
  .imgContainer {
    display: none;
  }
}

