@media screen and (max-width: 550px) {
  .home h1 {
    font-size: 2rem;
    padding: 0;
    margin-top: 5px;
    margin-bottom: -10px;
    color: white;
    margin-left: 10px;
  }
  .logo {
    width: 100%;
    display: flex;
    height: 13rem;
    justify-content: flex-start;
    align-items: flex-start;
    overflow: hidden;
    margin-top: 15%;
  }
  .logo img {
    object-fit: contain;
    margin-left: -80px;
    max-height: 20rem;
  }
  .imgContainer {
    display: none;
  }

  .container {
    flex-direction: column;
    align-items: center;
    padding-bottom: 10%;
    background: url("../../../../src/assets/gif/Authentication.gif");
    background-size: contain;
